/* http://meyerweb.com/eric/tools/css/reset/
   v5.0.1 | 20191019
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, menu, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

menu, ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

* {
  box-sizing: border-box;
}

.srOnly {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
}

.wrapper {
  position: relative;
  min-height: 100vh;
  padding: 3.8rem 3.2rem;
  background: top 12.1rem left 0.9rem/50.4rem 24.4rem no-repeat url(illustration-devices.svg);
}
@media (min-width: 35rem) {
  .wrapper {
    padding: 6.4rem;
    background-position: top 12.1rem center;
  }
}
@media (min-width: 48rem) {
  .wrapper {
    background-position: left 38rem top 21.1rem;
    background-size: auto;
  }
}
@media (min-width: 81.25rem) {
  .wrapper {
    padding: 6.4rem 16.5rem;
    background-position: right -26.8rem top 21.1rem;
  }
}
.wrapper::before {
  position: absolute;
  top: 0;
  right: 0;
  z-index: -10;
  width: 49%;
  height: 42.5rem;
  background: #eef3f6;
  border-bottom-left-radius: 6rem;
  content: "";
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  width: 2.4rem;
}
@media (min-width: 48rem) {
  .logo {
    width: 3.2rem;
  }
}

.menu_checkbox:checked ~ .menu_list {
  transform: translate(-50%, 0);
  visibility: visible;
  opacity: 1;
  transition: transform 0.3s, opacity 0.3s, visibility 0s;
}
@media (min-width: 35rem) {
  .menu_checkbox:checked ~ .menu_list {
    transform: none;
  }
}
.menu_checkbox:checked ~ .menu_hamburger > span:first-child {
  transform: rotate(45deg) translate(0.21rem, -0.37rem);
}
.menu_checkbox:checked ~ .menu_hamburger > span:nth-child(2) {
  transform: scale(0.2);
  opacity: 0;
}
.menu_checkbox:checked ~ .menu_hamburger > span:last-child {
  transform: rotate(-45deg) translate(0.21rem, 0.37rem);
}
.menu_hamburger {
  display: flex;
  flex-flow: column;
  justify-content: center;
  width: 4.8rem;
  height: 4.8rem;
  margin: -1.2rem;
  padding: 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  transition: background-color 0.15s;
}
@media (min-width: 35rem) {
  .menu_hamburger {
    display: none;
  }
}
.menu_hamburger:hover {
  background-color: rgba(91, 95, 113, 0.2);
}
.menu_hamburger:focus {
  outline: 0.1rem solid black;
}
.menu_hamburger > span {
  display: block;
  width: 100%;
  height: 0.2rem;
  background: #5b5f71;
  transition: transform 0.3s, opacity 0.3s;
}
.menu_hamburger > span:first-child {
  transform-origin: top left;
}
.menu_hamburger > span:last-child {
  transform-origin: bottom left;
}
.menu_hamburger > span + span {
  margin-top: 0.5rem;
}
.menu_list {
  position: absolute;
  top: 10rem;
  left: 50%;
  display: flex;
  flex-flow: column;
  align-items: center;
  width: calc(100% - 6.4rem);
  max-width: 31.1rem;
  padding: 2.4rem;
  background: white;
  border-radius: 0.2rem;
  box-shadow: 0 0.4rem 0.9rem rgba(0, 0, 0, 0.1);
  transform: translate(-50%, -2rem);
  visibility: hidden;
  opacity: 0;
  transition: transform 0.3s, opacity 0.3s, visibility 0s 0.3s;
}
@media (min-width: 35rem) {
  .menu_list {
    position: static;
    flex-flow: row;
    width: auto;
    max-width: none;
    padding: 0;
    background: transparent;
    box-shadow: none;
    transform: none;
    visibility: visible;
    opacity: 1;
    transition: none;
  }
  .menu_list > :nth-child(2) {
    margin-left: 4.2rem;
  }
  .menu_list > :nth-child(3) {
    margin-left: 4.1rem;
  }
  .menu_list > :nth-child(4) {
    margin-left: 4rem;
  }
  .menu_list > :last-child {
    margin-left: 4.6rem;
  }
}
.menu_list > * + * {
  margin-top: 2.4rem;
}
@media (min-width: 35rem) {
  .menu_list > * + * {
    margin-top: 0;
  }
}
.menu_list > li:last-child > .menu_link {
  color: #a0a1ac;
}
.menu_link {
  color: #242942;
  font: 700 1.8rem Barlow Condensed, sans-serif;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  text-decoration: none;
}
@media (min-width: 35rem) {
  .menu_link {
    font-size: 1.6rem;
    letter-spacing: 0.03em;
  }
}
.menu_link:hover {
  text-decoration: underline;
}
.menu_link:focus {
  outline: 0.1rem dashed #ff5c5c;
}
.menu_separator {
  width: 100%;
  height: 0.1rem;
  margin-bottom: 0;
  background: #e0e0e0;
  border: none;
}
@media (min-width: 35rem) {
  .menu_separator {
    width: 0.7rem;
    height: 0.7rem;
    border-radius: 50%;
  }
}

.intro {
  margin-top: 39rem;
}
@media (min-width: 35rem) {
  .intro {
    margin-top: 30rem;
  }
}
@media (min-width: 48rem) {
  .intro {
    margin-top: 16.2rem;
  }
}
.intro > .badge {
  margin-right: 1.6rem;
}
.intro_h1 {
  display: inline-block;
  color: #a0a1ac;
  font: 1.6rem Barlow Condensed, sans-serif;
  line-height: 160%;
  letter-spacing: 0.28em;
  text-transform: uppercase;
}
.intro_h2 {
  max-width: 30rem;
  margin-top: 1.6rem;
  color: #242942;
  font-weight: 700;
  font-size: calc( 4rem + 2.4 * ((100vw - 37.5rem) / 106.5) );
  font-family: Barlow Condensed, sans-serif;
  line-height: 100%;
  text-transform: uppercase;
}
@media (min-width: 48rem) {
  .intro_h2 {
    max-width: 35rem;
  }
}
@media (min-width: 81.25rem) {
  .intro_h2 {
    max-width: 55rem;
  }
}
.intro_text {
  max-width: 28rem;
  margin-top: 1.6rem;
  margin-bottom: 2.7rem;
  color: #5b5f71;
  font: 1.8rem Barlow, sans-serif;
  line-height: 145%;
}
@media (min-width: 48rem) {
  .intro_text {
    margin-top: 3.1rem;
    margin-bottom: 6.4rem;
  }
}
.intro > .btn {
  margin-right: 2rem;
}
@media (min-width: 48rem) {
  .intro > .btn {
    margin-right: 2.4rem;
  }
}
.intro_label {
  color: #a0a1ac;
  font: 1.4rem Barlow Condensed, sans-serif;
  line-height: 185%;
  letter-spacing: 0.18em;
  white-space: nowrap;
  text-transform: uppercase;
}
@media (min-width: 48rem) {
  .intro_label {
    font-size: 1.6rem;
    letter-spacing: 0.29em;
  }
}

.badge {
  display: inline-block;
  padding: 0.2rem 1rem 0.4rem;
  color: white;
  font: 700 1.6rem Barlow Condensed, sans-serif;
  text-transform: uppercase;
  background: #242942;
  border-radius: 1.2rem;
}

.btn {
  padding: 1.1rem 2.4rem 1rem;
  color: white;
  font: 700 1.6rem Barlow Condensed, sans-serif;
  text-transform: uppercase;
  background: #ff5c5c;
  border: none;
  border-radius: 0.4rem;
  cursor: pointer;
  transition: filter 0.15s, transform 0.15s;
}
@media (min-width: 48rem) {
  .btn {
    padding: 1.3rem 3.2rem;
    font-size: 1.8rem;
    letter-spacing: 0.01em;
  }
}
.btn:hover {
  filter: brightness(1.4);
}
.btn:focus {
  outline: 0.1rem dotted #ff5c5c;
}
.btn:active {
  transform: translateY(0.2rem);
}